import stationSwitch from "@/components/stationComponent/stationSwitch.vue";
import statisticalEcharts from "./components/statisticalEcharts.vue";
import up from "@/assets/run_powerStaic/arrowGreen.png";
import down from "@/assets/run_powerStaic/arrowRed.png";
import loadBox from "@/components/loadingBox/loadingBox.vue";
import alarmNumberDialog from "./components/alarmNumberDialog.vue";
import {
  myelecStationList,
  overviewm1,
  overviewm2,
  overviewtop,
  pointList,
} from "@/network/powerStaic";
export default {
  components: {
    statisticalEcharts,
    stationSwitch,
    loadBox,
    alarmNumberDialog,
  },
  data() {
    return {
      eqIndex: 0,
      showStation: false,
      pdfSubTitle: [
        "设备数量",
        "巡检计划",
        // "电压等级（kV）",
        // "主变数（台）",
