      pdfSubTitle2Value: [],
      overviewMsg: {},
      overviewm1Msg: {},
      eqmsLength: 0,
      elecStationId: "",
      eqmArry: [],
      eqmsLength: 0,
      stationList: [],
      elecStationLoad: false,
      overviewm2Obj: {},
      eqPointValues: [],
      companyId: sessionStorage.getItem("companyId"),
        highElec:["001","高压电站","2","2"],
        centerChange:["002","中心变","3","2"],
        totalName:["高压电站","中心变"]
    };
  },
  created() {
    //获取电站ID
    this.$bus.$on("busElecStationId", (val) => {
      this.overviewm1Fun(val); //获取配电房首页内容
      this.overviewm2Fun(val); //获取配电房首页内容
      this.overviewtopFun(val); //获取配电房首页内容
    });

    this.$bus.$on("busHideSwitch", (msg) => {
      this.showStation = false;
    });
  },
  mounted() {
    this.myelecStationListFun();
  },
  methods: {
    /**
     * 点击告警数量
     * **/
    clickEvent(index) {
      if (index == 1) {
        this.$refs.aNDialog.showDialogFun(
          this.overviewm2Obj.companyId,
          this.overviewm2Obj.elecStationName
        );
      }
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = val;
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
    /**
     * 电量总览上升率或者下降率
     * **/
    getRate(rate) {
      if (rate) {
        if (rate > 0) {
          return `<img src="${down}" class="downImg" style="transform: rotate(180deg);" alt="" />
              <span style="color:#ed2323">${parseInt(rate)}%</span>`;
        } else {
          return `<img src="${up}" class="upImg" alt="" />
              ${parseInt(rate)}%`;
        }
      }
    },
    reduce() {
      if (this.eqIndex == 0) {
        this.eqIndex = this.eqmsLength - 1;
      } else {
        this.eqIndex--;
      }
      this.pointListFun(this.eqmArry[this.eqIndex].elecEqmId);
    },
    add() {
      if (this.eqIndex == this.eqmsLength - 1) {
        this.eqIndex = 0;
      } else {
        this.eqIndex++;
      }
      this.pointListFun(this.eqmArry[this.eqIndex].elecEqmId);
    },
    myelecStationListFun() {
        let errcode =0
        if (errcode == 0) {
            this.stationList = [];
            content.forEach((item) => {
                this.stationList.push({
                    stationName: item.elecStationName,
                    stationId: item.elecStationId,
                    // stationName:this.totalName
                });
            })
            this.overviewm1Fun(this.stationList[0].stationId); //获取配电房首页内容
            this.overviewm2Fun(this.stationList[0].stationId); //获取配电房首页内容
            this.overviewtopFun(this.stationList[0].stationId); //获取配电房首页内容
        }
      // myelecStationList()
      //   .then((res) => {
      //     const { errcode, errmsg, content } = res;
      //     if (errcode == 0) {
      //       this.stationList = [];
      //       content.forEach((item) => {
      //         this.stationList.push({
      //           stationName: item.elecStationName,
      //           stationId: item.elecStationId,
      //             // stationName:this.totalName
      //         });
      //       })
      //       this.overviewm1Fun(this.stationList[0].stationId); //获取配电房首页内容
      //       this.overviewm2Fun(this.stationList[0].stationId); //获取配电房首页内容
      //       this.overviewtopFun(this.stationList[0].stationId); //获取配电房首页内容
      //     }
      //   })
      //   .catch((err) => {});
    },
    overviewm1Fun(val) {
      let that = this;
      let elecStationId = val;
      let errcode =0
      that.elecStationId = elecStationId;
        if (errcode == 0) {
            that.overviewm1Msg = JSON.parse(JSON.stringify(content));
            that.overviewMsg = Object.assign(that.overviewMsg, content);
        }
      // overviewm1(elecStationId)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       that.overviewm1Msg = JSON.parse(JSON.stringify(content));
      //       that.overviewMsg = Object.assign(that.overviewMsg, content);
      //     }
      //   })
      //   .catch((err) => {});
    },
    overviewm2Fun(val) {
      let that = this;
      let elecStationId = val;
      that.elecStationId = elecStationId;

        const { content, errcode, errmsg } = res;
        if (errcode == 0) {
            that.eqmsLength = content.eqms.length;
            that.eqmArry = content.eqms;
            that.overviewMsg = Object.assign(that.overviewMsg, content);
            that.overviewm2Obj = content;
            that.pointListFun(content.eqms[0].elecEqmId);

        }

      // overviewm2(elecStationId)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       that.eqmsLength = content.eqms.length;
      //       that.eqmArry = content.eqms;
      //       that.overviewMsg = Object.assign(that.overviewMsg, content);
      //       that.overviewm2Obj = content;
      //       that.pointListFun(content.eqms[0].elecEqmId);
      //     }
      //       console.log("6666666666"+that.overviewm2Obj)
      //   })
      //   .catch((err) => {});
    },
    pointListFun(eqmId) {
        let errcode =0
        if (errcode == 0) {
            this.eqPointValues = content;
        }
      // pointList(eqmId)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       this.eqPointValues = content;
      //     }
      //   })
      //   .catch((err) => {});
    },
    overviewtopFun(val) {
      let that = this;
      let elecStationId = val;
      that.elecStationId = elecStationId;
        if (errcode == 0) {
            that.pdfSubTitleValue = [
                // content.sum_ElecEqmTable,
                // content.sum_ElecStationCheckPlan,
                12,23
            ];
            that.pdfSubTitle2Value = [
                // content.sum_date,
                // content.sum_alarm,
                // content.sum_quexian,
                11.1,0,1
            ];
        }
      // overviewtop(elecStationId)
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       that.pdfSubTitleValue = [
      //         content.sum_ElecEqmTable,
      //         content.sum_ElecStationCheckPlan,
      //       ];
      //       that.pdfSubTitle2Value = [
      //         content.sum_date,
      //         content.sum_alarm,
      //         content.sum_quexian,
      //       ];
      //     }
      //   })
      //   .catch((err) => {});
    },
    findValue(name) {
      // let nowValue = "";
      // this.eqPointValues.forEach((item) => {
      //   if (item.pointKey == name) {
      //     nowValue = this.toFixedTwo(item.dATA_VALUE);
      //   }
      // });
      // return nowValue;
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = Number(val);
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>
