.chevron {
  position: absolute;
  width: 12px;
  height: 2px;
  opacity: 0;
  animation: move-chevron 2.5s linear infinite;
}

.chevron:first-child {
  animation: move-chevron 2.5s linear 0.5s infinite;
}

.chevron:nth-child(2) {
  animation: move-chevron 2.5s linear 1s infinite;
}
.chevron:nth-child(3) {
  animation: move-chevron 2.5s linear 1.5s infinite;
}
.chevron:nth-child(4) {
  animation: move-chevron 2.5s linear 2s infinite;
}

.chevron:before,
.chevron:after {
  content: "";
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: #ffffff;
}

.chevron:before {
  left: 0;
  transform: skewY(45deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skewY(-45deg);
}

@keyframes move-chevron {
  20% {
    opacity: 1;
  }
  40% {
    opacity: 1;
    transform: translateY(20px);
  }
  60% {
    opacity: 1;
    transform: translateY(30px);
  }
  80% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(50px);
  }
}
